-prefixes-sort */\n\n\n/*\n * REM单位基准\n * 设置100px主要为了方便计算 在页面任意地方 1rem = 100px\n * 关于为何不使用10px：PC chrome有12px最小字号限制，若使用10px会造成pc调试困难带来不必要的麻烦\n * 变换规则：\n * 1.屏幕宽度360px以下，按0.9倍缩小\n * @author yangfan\n */\n\nhtml {\n    font-size: 100px;\n}\n\nbody {\n    font: 14px Arial, Helvetica, sans-serif;\n    font-size-adjust: none;\n    color: #333;\n    background: #fff;\n    -webkit-text-size-adjust: none;\n}\n\n* {\n    margin: 0;\n    padding: 0;\n    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);\n}\n\n@media screen and (max-width: 360px) {\n    html {\n        font-size: 90px !important;\n    }\n}\n\n\n/*\n * 卡片基础样式\n * @author yangfan\n */\n\n.c-result {\n    margin: 0;\n    padding: 0;\n    border: 0 none;\n    background: none;\n}\n\n.c-blocka {\n    display: block;\n}\n\n\n/* csshint-disable disallow-overqualified-elements */\n\na .c-title,\na.c-title {\n    font: 18px/28px Arial, Helvetica, sans-serif;\n    color: #000;\n}\n\na:visited .c-title,\na:visited.c-title {\n    color: #999;\n}\n\n.sfa-view a:visited .c-title,\n.sfa-view a:visited.c-title,\n.sfa-view .c-title {\n    font: 18px/28px Arial, Helvetica, sans-serif;\n    color: #000;\n}\n\n\n/* csshint-enable disallow-overqualified-elements */\n\n.c-title-noclick,\n.c-title {\n    font: 18px/28px Arial, Helvetica, sans-serif;\n    color: #999;\n}\n\n.c-title-nowrap {\n    position: relative;\n    box-sizing: border-box;\n    width: 100%;\n    padding-right: 33px;\n    white-space: nowrap;\n}\n\n.c-title-nowrap .c-text {\n    display: inline-block;\n    vertical-align: middle;\n}\n\n.c-title-nowrap .c-title-text {\n    display: inline-block;\n    overflow: hidden;\n    max-width: 100%;\n    vertical-align: bottom;\n    white-space: nowrap;\n    text-overflow: ellipsis;\n}\n\n.c-font-24 {\n    font: 24px/34px Arial, Helvetica, sans-serif;\n}\n\n.c-font-18 {\n    font: 18px/28px Arial, Helvetica, sans-serif;\n}\n\n.c-font-18 {\n    font: 18px/28px Arial, Helvetica, sans-serif;\n}\n\n.c-font-16 {\n    font: 16px/26px Arial, Helvetica, sans-serif;\n}\n\n.c-font-14 {\n    font: 14px/24px Arial, Helvetica, sans-serif;\n}\n\n.c-font-12 {\n    font: 12px/22px Arial, Helvetica, sans-serif;\n}\n\n.c-font-10 {\n    font: 10px/20px Arial, Helvetica, sans-serif;\n}\n\n.c-price {\n    font: 19px/29px Arial, Helvetica, sans-serif;\n    color: #f60;\n}\n\n.c-title-wrap {\n    display: block;\n}\n\n.c-title-nowrap {\n    display: none;\n}\n\n@media (min-width: 376px) {\n    .c-title {\n        display: block;\n        overflow: hidden;\n        max-width: 100%;\n        vertical-align: middle;\n        white-space: nowrap;\n        text-overflow: ellipsis;\n    }\n    .c-title-nowrap {\n        display: block;\n        overflow: visible;\n    }\n    .c-title-wrap {\n        display: none;\n    }\n}\n\n.c-abstract {\n    color: #555;\n}\n\n.c-showurl {\n    font: 14px/24px Arial, Helvetica, sans-serif;\n    color: #999;\n}\n\n.c-gray {\n    font: 14px/24px Arial, Helvetica, sans-serif;\n    color: #999;\n}\n\n.c-moreinfo {\n    font: 14px/24px Arial, Helvetica, sans-serif;\n    text-align: right;\n    color: #555;\n}\n\n.c-foot-icon {\n    position: relative;\n    top: .02rem;\n    display: inline-block;\n    background: url(//m.baidu.com/static/search/sprite.png) no-repeat;\n    -webkit-background-size: 1.9rem 1.42rem;\n    background-size: 1.9rem 1.42rem;\n}\n\n.c-foot-icon-16 {\n    width: .16rem;\n    height: .13rem;\n}\n\n.c-foot-icon-16-aladdin {\n    display: none;\n    background-position: 0 -.98rem;\n}\n\n.c-foot-icon-16-lightapp {\n    background-position: -.2rem -.98rem;\n}\n\n.c-visited,\n.c-visited .c-title,\n.c-visited.c-title {\n    color: #999 !important;\n}\n\n\n/*\n * 栅格基础框架\n * 页面等分为12n，其中内容31，间距24，共(31+24)*12=660\n * 1n = 1/12 xn = x/12 左右各留12/660 padding\n * 最左&最右的padding采用c-row -12/660 margin 消除\n * @ BUG\n * 安卓QQ浏览器 X5内核(026008GPU-S)存在弹性盒子兼容性问题-对应浏览器版本6.0.0.1550\n * 实验版X5内核(036006GPU)修复正常-对应浏览器版本2.2.0.60\n * 因此舍弃弹性盒模型自动换行属性\n * @author yangfan\n */\n\n.c-containe