import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

export default new Router({
    routes: [{
        path: '/',
        component: () => System.import('@/pages/index/index.vue')
    },
    {    // 首页
        path: '/index',
        component: () => System.import('@/pages/index/index.vue')
    },
    {    // 服务页
        path: '/service',
        component: () => System.import('@/pages/service/index.vue')
    },
    {    // 查询
        path: '/query',
        component: () => System.import('@/pages/query/index.vue')
    },
    {    // 新闻
        path: '/news',
        component: () => System.import('@/pages/news/index.vue')
    },
     {    // 办事知道南
        path: '/guide',
        component: () => System.import('@/pages/guide/index.vue')
    },{
        //  搜索
       path: '/search',
        component: () => System.import('@/pages/search/index.vue')
     },
     {
        //  详情
       path: '/detail',
        component: () => System.import('@/pages/detail/index.vue')
     },
     {
        //  写信
       path: '/letter',
        component: () => System.import('@/pages/letter/index.vue')
     },
     {
        //  写信
       path: '/list',
        component: () => System.import('@/pages/list.vue')
     }
   ]

});



